body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.side-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    color: #fff;
    z-index: 9999;
    overflow-y: scroll;
    -webkit-transform: translate(-20rem);
    transform: translate(-20rem);
    transition: all .5s ease;
    background-color: black
}

.visible {
    -webkit-transform: translate(0);
            transform: translate(0);
}
.food-list {
    list-style-type: none;
    padding-left: 0;
}
.food-item {
    word-wrap: break-word;  
    padding: 0.5em;
    font-size: 1em;
    border-top: 1px solid white;
    cursor: pointer;
}
.food-item:hover{
    background-color: white;
    color: black;
}
.food-item:focus{
    background-color: white;
    color: black;
}
.menu-title {
    margin-left: 0.5em;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.food-finder {
    margin-left: 0.5em;
    padding-top: 0.5em; 
    padding-bottom: 0.5em; 
    width: 80%
}

.wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  transition: all .5s ease;
  width: 100%;
  height: 100%;
  left: 0;
}

.slide {
  left: 20rem;
}

.nav {
    width: 100%;
    padding: 1rem 0;
    background-color: black;
    display: flex;
    align-items: center;
    z-index: 1;
}

.title {
    color: #fff;
    flex-grow: 1;
    text-align: center;
    font-size: 1.8rem;
    margin: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.nav-burger {
    width: 2rem;
    cursor: pointer;
    margin-left: 2rem;
}

.nav-burger>:not(:last-child) {
    margin-bottom: .4rem;
}

.bar {
    width: 100%;
    height: .3rem;
    background-color: #fff;
    border-radius: .5rem;
}

.map{
  width: 100% !important;
  height: calc(100% - 63px) !important;
}
